<template>
  <div>
    <van-nav-bar title="走势图"
                 left-arrow
                 @click-left="onClickLeft"></van-nav-bar>
    <div class="button"
         @click="showPlay=!showPlay">
      {{selectObj.key}}<i class="van-icon van-icon-arrow-down"></i>
    </div>
    <div class="stage">
      <span>期数</span>
      <van-stepper v-model="stepper" />
      <van-button round
                  size="mini"
                  type="danger">查询</van-button>

    </div>
    <div class="button-check-group">
      <div class="button-check"
           @click="checkGroup('w')"
           :class="w?'':'disuse'">万位</div>
      <div class="button-check"
           @click="checkGroup('q')"
           :class="q?'':'disuse'">千位</div>
      <div class="button-check"
           @click="checkGroup('b')"
           :class="b?'':'disuse'">百位</div>
      <div class="button-check"
           @click="checkGroup('s')"
           :class="s?'':'disuse'">十位</div>
      <div class="button-check"
           @click="checkGroup('g')"
           :class="g?'':'disuse'">个位</div>
    </div>
    <div class="canvas">
      <div class="title">{{selectObj.key}}</div>
      <canvas id="myChart"
              :width="viewWidth"
              :height="260"></canvas>
    </div>

    <van-popup v-model="showPlay"
               position="bottom">
      <van-picker :columns='columns'
                  show-toolbar
                  @confirm='confirm'
                  @cancel="showPlay =false"></van-picker>
    </van-popup>

  </div>
</template>
<script>
import { Popup, Picker, Stepper, Button } from "vant";
import options from '@/mixins/options.js'

const F2 = require('@antv/f2');

export default {
  mixins: [options],
  data () {
    return {
      selectObj: {
        key: '默认',
        value: ""
      },
      showPlay: false,
      columns: [],
      columnsData: [{ key: 'name1', value: 1 }, { key: 'name2', value: 2 }, { key: 'name3', value: 3 }],
      stepper: 10,
      w: true,
      q: true,
      b: true,
      s: true,
      g: true,
      data: [
        { genre: '0', sold: 275 },
        { genre: '1', sold: 115 },
        { genre: '2', sold: 120 },
        { genre: '3', sold: 350 },
        { genre: '4', sold: 150 },
        { genre: '5', sold: 275 },
        { genre: '6', sold: 115 },
        { genre: '7', sold: 120 },
        { genre: '8', sold: 350 },
        { genre: '9', sold: 150 },
      ]

    }
  },
  components: {
    [Popup.name]: Popup,
    [Picker.name]: Picker,
    [Stepper.name]: Stepper,
    [Button.name]: Button,


  },
  methods: {
    checkGroup (key) {
      switch (key) {
        case 'w':
          this.w = !this.w
          break;
        case 'q':
          this.q = !this.q

          break;
        case 'b':
          this.b = !this.b

          break;
        case 's':
          this.s = !this.s

          break;
        case 'g':
          this.g = !this.g

          break;

      }
    },
    onRefresh (item) {
      if (item) this.selectObj = item
      console.log(this.selectObj)
    },
    changeWays () {
      console.log('玩法')
    },
    confirm (val, index) {
      this.showPlay = false;
      console.log('确认', val, index)
      this.onRefresh(this.columnsData[index])
    }
  },
  created () {
    this.columns = this.columnsData.map(item =>
      item.key
    )
    this.onRefresh(this.columnsData[0])
  },
  mounted () {
    const chart = new F2.Chart({
      id: 'myChart',
      pixelRatio: window.devicePixelRatio // 指定分辨率
    });
    // load the data
    chart.source(this.data);
    // draw a column chart
    chart.interval().position('genre*sold').color('red');
    chart.render();
  },
  computed: {
    result: function () {
      return [
        this.w,
        this.q,
        this.b,
        this.s,
        this.g,
      ]
    },
    viewWidth: function () {
      return window.innerWidth
    },
    viewHeight: function () {
      return window.innerWidth / 320 * 298
    }
  },
  watch: {
    result: function (val) {
      console.log(val)
    }
  }

}
</script>
