<template>
  <div>
    <van-nav-bar title="专家计划" />
    <cp-notice></cp-notice>
    <van-list v-model="loading"
              :finished="finished"
              finished-text="没有更多了"
              @load='load'>
      <expert-item v-for="item in objList"
                   :key="item.key"
                   :obj='item'></expert-item>
    </van-list>
  </div>
</template>
<script>
import { NavBar, PullRefresh, List } from "vant";
import ExpertItem from '@/components/ExpertItem'

export default {
  data () {
    return {
      objList: [],
      loading: true,
      finished: false
    }
  },
  methods: {
    load () {
      let that = this
      setTimeout(() => {
        for (let index = 0; index < 10; index++) {
          that.objList.push({
            headUrl: 'http://pic136.nipic.com/file/20170725/10673188_152559977000_2.jpg',
            name: '乔峰',
            title: '副标题'
          })
        }
        // 加载状态结束
        that.loading = false;

        // 数据全部加载完成
        if (that.list.length >= 40) {
          this.finished = true
        }
      }, 500);
    }
  },
  created () {
    this.load()
  },
  components: {
    [PullRefresh.name]: PullRefresh,
    [List.name]: List,
    [NavBar.name]: NavBar,
    ExpertItem
  }
}
</script>
